* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    outline: none;
}

.contain-1 {
    width: fit-content;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    margin-top: 150px;
}

.login {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.form-1 {
    max-width: 380px;
    width: 100%;
    font-family: 'Montserrat', sans-serif;
}

form input[type="email"] , input[type="Password"] {
    width: 100%;
    font-size: 13px;
    padding: 13px;
    padding-left: 20px;
    border: none;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    border: 3px solid #ededed;
    border-radius: 50px;
}

.label {
    display: block;
    margin-top: 20px;
    margin-bottom: 8px;
}

form h1 {
    font-family: 'Poppins', sans-serif;
    color: #7673c8;
    /* color: #c89173; */
    margin-bottom: 10px;
}   

small {
    font-size: 12px;
    color: #c1c1c1;
}

.log {
    width: 100%;
    font-size: 15px;
    padding: 10px;
    border: none;
    margin-top: 20px;
    margin-bottom: 20px;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    border: 3px solid #ededed;
    border-radius: 50px;
    /* the real one for all border-radius: 5px; */
    color: white;
    border: 3px solid #7673c8;
    background-color: #7673c8;
    transition: .4s;
}

.image {
    width: 400px;
    height: 515px;
    border-radius: 40px;
    background-color: rgb(191, 191, 191);
    background-image: url(https://unsplash.com/photos/5GJEOFzGIf4);
    background-position: center;
    background-size: 400px;
    display: flex;
    flex-direction: column;
    animation: pic 20s infinite; 
}

@keyframes pic {
    0%   {
        background-image: url(https://img.freepik.com/free-vector/healthcare-background-with-medical-symbols-hexagonal-frame_1017-26363.jpg?w=1380&t=st=1690047140~exp=1690047740~hmac=abca436f2c029422cc88884485cf954ec5081eb2ba493150dea749763affa3ec);
    }
    25%  {
        background-image: url(https://img.freepik.com/free-photo/young-handsome-physician-medical-robe-with-stethoscope_1303-17818.jpg?w=1380&t=st=1690047218~exp=1690047818~hmac=b280d8b74c0115d01dd6008cb33b19c6faa5b481ba796bb16484e3b77d984a41);
    }
    50%  {
        background-image: url(https://img.freepik.com/free-photo/medical-banner-with-doctor-wearing-goggles_23-2149611193.jpg?w=1380&t=st=1690047243~exp=1690047843~hmac=411a51513f8c2e3acc167714031fb0fef2b562c8de68654c89c5f639a8fde288);
    }
    75% {
        background-image: url(https://img.freepik.com/free-photo/medicine-capsules-global-health-with-geometric-pattern-digital-remix_53876-126742.jpg?w=1380&t=st=1690047271~exp=1690047871~hmac=df7e9af659508908ca20e0d0b3077c4836bd2444c71ef79e3a43c30cbc60c71c);
    }
    100% {
        background-image: url(https://img.freepik.com/free-vector/medical-design-poster-with-original-medicinal-capsule-consisting-red-white-parts-different-medical-objects_1284-53615.jpg?w=826&t=st=1690047290~exp=1690047890~hmac=11c7b932e9bde84b1dad22bbc4530894c125abb82ce9caa19c086e309a024514);
    }
}

.image h1 {
    margin: 20px;
    color: #ffffff;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
}

.btn-1 {
    position: absolute;
    top: 0;
    left: 0;
    margin: 20px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: #c1c1c1;
    background-color: transparent;
    border: 2px solid #ededed;
    border-radius: 50px;
    padding-right: 10px;
}



@media only screen and (max-width: 860px) {
    .image {
      display: none;
    }
}

